const swiperTtems = new Swiper('.services__items', {
  spaceBetween: 10,
  slidesPerView: 6,
  navigation: {
    nextEl: '.swiper-button-next',
    prevEl: '.swiper-button-prev',
  },
  freeMode: true,
  watchSlidesVisibility: true,
  watchSlidesProgress: true,
  breakpoints: {
    850: {
      spaceBetween: 10,
      slidesPerView: 6,
    },
    576: {
      spaceBetween: 10,
      slidesPerView: 4,
    },
    320: {
      spaceBetween: 10,
      slidesPerView: 2,
    },
  }
})

const swiperInner = new Swiper('.services__inner-slider', {
  slidesPerView: 1,
  thumbs: {
    swiper: swiperTtems
  }
});

const swiperArticles = new Swiper('.articles__slider', {
  slidesPerView: 3,
  spaceBetween: 24,
  navigation: {
    nextEl: '.articles__slider .swiper-button-next',
    prevEl: '.articles__slider .swiper-button-prev',
  },
  breakpoints: {
    920: {
      spaceBetween: 10,
      slidesPerView: 3,
    },
    600: {
      spaceBetween: 10,
      slidesPerView: 2,
    },
    320: {
      spaceBetween: 10,
      slidesPerView: 1,
    },
  }
});

// Цифры у слайдера
swiperArticles.on('slideChange', function (slider) {
  const currentBlock = document.querySelector('.number-start');
  const activeSlide = slider.realIndex + 1;
  currentBlock.innerHTML = activeSlide;
});

// Карта
ymaps.ready(function () {
  let footerMap = new ymaps.Map("feedback__map", {
    center: [47.10871, 37.64266],
    zoom: 16,
    behaviors: ['drag'],
    controls: ['zoomControl'],
  })

  // Метка на карте
  let myPlacemark = new ymaps.Placemark(footerMap.getCenter(), {
    balloonContent: 'Офис компании'
  }, {
    iconLayout: 'default#image',
    iconImageHref: 'images/map.svg',
    iconImageSize: [42, 60],
    iconImageOffset: [-37, -100]
  });

  footerMap.geoObjects.add(myPlacemark)
});

// Бургер
var button = document.querySelector('.menu__btn');
var menu = document.querySelector('.menu__list');

button.addEventListener('click', function () {
  menu.classList.toggle('menu__list--active');
});




